@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@400;700&display=swap);
.modal{position:fixed;top:0;right:0;bottom:0;left:0;background:rgba(0,0,0,.6);z-index:100;display:flex;align-items:center;justify-content:center;overflow:auto;padding:10px;opacity:0;pointer-events:none;transition:.5s}.modal--active{opacity:1;pointer-events:all}.modal--active .moda-content{-webkit-transform:translateY(0);transform:translateY(0)}.modal-inner{width:100%;min-height:100%;display:flex;align-items:center;justify-content:center}.moda-content{max-width:600px;width:100%;text-align:center;background:#fff;padding:30px;border-radius:20px;box-shadow:6px 6px 10px rgba(0,0,0,.3);position:relative;-webkit-transform:translateY(-200px);transform:translateY(-200px);transition:.5s}.moda-content iframe{border-radius:20px}.modal__close{position:absolute;top:-10px;right:-10px;width:30px;height:30px;display:flex;align-items:center;justify-content:center;background:#fff;border-radius:50%}.modal__close::before,.modal__close::after{position:absolute;content:"";width:20px;height:4px;background:#000;border-radius:10px;transition:.5s}.modal__close::before{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.modal__close::after{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.modal__close:hover::before{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.modal__close:hover::after{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.modal__close:active::before{-webkit-transform:rotate(-30deg);transform:rotate(-30deg)}.modal__close:active::after{-webkit-transform:rotate(30deg);transform:rotate(30deg)}
.slider__item{min-height:100vh;background-repeat:no-repeat;background-size:cover;background-position:top center;display:flex;align-items:center;justify-content:space-around;z-index:2;padding:100px 15px 50px}.slider__item::before{position:absolute;content:"";width:100%;height:100%;top:0;bottom:0;background:rgba(0,0,0,.5);z-index:1}.slider__item-content{max-width:600px;color:#fff;z-index:10;-webkit-transform:translateY(-100px);transform:translateY(-100px);opacity:0;transition:.5s}.slider__item-title{font-size:40px;font-weight:700;margin-bottom:20px}.slider__item-desc{font-size:18px;line-height:1.3em}.slider__item-poster{z-index:10;border-radius:20px;transition:.5s;-webkit-transform:scale(0);transform:scale(0)}.slider__item-button{padding:10px 40px;border:2px solid #fff;margin-top:20px;border-radius:20px;position:relative;display:flex;align-items:center;justify-content:center;font-weight:700}.slider__item-button:active{bottom:-1px}.slider__item-button svg{width:15px;margin-right:10px}.slider__item-button svg path{stroke:#fff;fill:transparent;transition:.5s}.slider__item-button:hover{background:rgba(0,0,0,.5)}.slider__item-button:hover path{fill:#fff}.swiper-slide-active .slider__item-content{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}.swiper-slide-active .slider__item-poster{-webkit-transform:scale(1);transform:scale(1)}@media(max-width: 960px){.slider__item-content{max-width:400px}.slider__item-poster{max-width:200px}}@media(max-width: 650px){.slider__item-content{max-width:300px}}@media(max-width: 550px){.slider__item{flex-direction:column;align-items:center}.slider__item-content{max-width:500px;text-align:center;margin-bottom:15px}.slider__item-button{margin:20px auto 0}}@media(max-width: 550px){.slider__item-title{font-size:30px}.slider__item-desc{font-size:16px}}
.movie-list{padding:15px}.movie-list .swiper-slide{width:20%}.movie-list__title{font-size:36px;color:#fff;font-weight:700;margin-bottom:15px}.movie-list__item{width:100%}.movie-list__content-img{max-width:265px}.movie-list__content-img img{width:265px;height:400px;border-radius:20px}.movie-list__content-img-none{background:#9b9b9b;width:265px;height:400px;border-radius:20px}.movie-list__content-title{max-width:265px;font-size:20px;text-align:center;color:#fff;margin-top:10px}@media(max-width: 1350px){.movie-list .swiper-slide{width:30%}}@media(max-width: 900px){.movie-list .swiper-slide{width:45%}}@media(max-width: 620px){.movie-list .swiper-slide{width:60%}}@media(max-width: 500px){.movie-list .swiper-slide{width:80%}}@media(max-width: 360px){.movie-list .swiper-slide{width:90%}}
.header{position:fixed;color:#fff;padding:10px 15px;max-width:1920px;width:100%;left:0;right:0;margin:0 auto;height:60px;display:flex;align-items:center;border-bottom:1px solid #9b9b9b;z-index:50;transition:.5s}.header--sticky{background:#292d32;border-color:transparent}.header-inner{width:100%;display:flex;align-items:center;justify-content:space-between}.header__logo{display:flex;align-items:center;cursor:pointer}.header__logo-title{font-size:30px;font-weight:700;margin-left:10px}.nav__list{display:flex}.nav__list-item{margin-right:15px;font-size:20px;font-weight:700;cursor:pointer;transition:.5s}.nav__list-item:last-child{margin-right:0}.nav__list-item:hover{color:#e44f4f}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}*{box-sizing:border-box;-webkit-tap-highlight-color:transparent}html,body{height:100%;font-family:"Balsamiq Sans",cursive;background:#292d32}ol,ul{list-style:none}button{border:none;outline:none;background:none;cursor:pointer;font-family:"Balsamiq Sans",cursive;color:#fff;font-size:16px;transition:.5s}
